.button-container {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.circle {
    position: absolute;
    border: 1px solid var(--color-primary);
    width: 119px;
    height: 113px;
    border-radius: 112px;
}

.button-content {
    align-items: center;
    display: flex;
    margin-left: 50px;
}

.arrow {
    width: 200px;
}
